"use client"
import ProjectCard from '../../components/ProjectCard'
import BriefingHeader from '../../components/BriefingHeader'
import ContactCTA from '../../components/ContactCTA'
import { projects } from '../../data/projects'
import { useState } from 'react'

const modes = [
  { key: 'all',                       label: 'All Projects'              },
  { key: 'venture-builder',           label: 'Venture Builder'           },
  { key: 'product-leadership-partner', label: 'Product Leadership'       },
  { key: 'design-systems',            label: 'Design Systems'            },
  { key: 'regulated-systems',         label: 'Regulated Systems'         },
]

export default function WorkIndex() {
  const [filter, setFilter] = useState('all')
  const list = filter === 'all'
    ? projects
    : projects.filter(p => p.engagementModes.includes(filter))

  return (
    <>
      {/* Page header */}
      <section className="band">
        <div className="max-w-[90rem] mx-auto px-6 lg:px-12">
          <BriefingHeader
            id="§ Work — Case Studies"
            title="Case Studies"
            objective="Selected projects demonstrating structured thinking across consumer platforms, regulated systems, logistics, energy, and community infrastructure."
          />

          {/* Filter bar */}
          <div
            className="flex gap-2 items-center flex-wrap"
            role="group"
            aria-label="Filter projects by engagement model"
          >
            {modes.map(m => (
              <button
                key={m.key}
                onClick={() => setFilter(m.key)}
                className={`filter-pill ${filter === m.key ? 'active' : ''}`}
                aria-pressed={filter === m.key}
              >
                {m.label}
              </button>
            ))}
          </div>

          {/* Grid */}
          <div
            className="mt-10 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6"
            role="list"
            aria-label="Project case studies"
          >
            {list.map(p => (
              <div key={p.slug} role="listitem">
                <ProjectCard project={p} />
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Contact CTA */}
      <section className="band band-surface">
        <div className="max-w-[90rem] mx-auto px-6 lg:px-12">
          <ContactCTA />
        </div>
      </section>
    </>
  )
}

